<template>
  <div class="header-imgmore no-modal-shelter" style="background: #0871dc;">
    <div style="width:10%">
    <span class="spnb">Shattrath</span>
    </div>
    <div style="width:73%;text-align: center;">
        <span class="text">{{titleName}}</span>
    </div>
    <div style="width:17%;float: right">
        <slot name="header-action"></slot>
    </div>
    </div>
</template>

<script>
import { BTN_TEXT } from '@/utils/constantsFile';
export default {
  name: 'sHeader',
  props: {
    // 标题名称
    titleName: {
      type: String,
      default: ''
    },
    // 判断搜索按钮是否显示
    flag: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      title: ''
    };
  },
  methods: {
    getTitle() {
      if (this.titleName === '') {
        this.title = this.$route.name;
      } else {
        this.title = this.titleName;
      }
    }
  },
  watch: {
    titleName(newVal, oldVal) {
      this.title = newVal;
    }
  },
  created() {
    this.getTitle();
  },
  components: {
  }
};
</script>

